@font-face {
  font-family: 'font';
  src: url('./font.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
}

.app-container {
  width: 100%;
  min-height: 100vh;
}

.hero-section {
  height: 100vh;
  width: 100%;
  background-image:
    linear-gradient(to bottom, rgba(0,0,0,0) 40%, rgba(0,0,0,1) 100%),
    url('./Images/main.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.hero-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex: 1;
  justify-content: center;
  max-width: 100%;
}

.title {
  width: min(30vw, 500px);
  height: auto;
  margin-bottom: 40rem;
  min-width: 250px;
}

.join-link {
  color: white;
  font-family: 'font', sans-serif;
  font-size: clamp(1.2rem, 4vw, 2rem);
  text-decoration: none;
  transition: opacity 0.3s ease;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 99;
}

.join-link:hover {
  opacity: 0.8;
}

.top-nav {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 10;
}

.nav-links {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.nav-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 0.5rem 1rem;
  border-radius: 2rem;
  transition: all 0.3s ease;
  color: white;
  font-family: 'font', sans-serif;
  font-size: 0.9rem;
}

.nav-item:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.nav-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
  filter: brightness(0) saturate(100%) invert(1);
}

.footer-section {
  background-image: linear-gradient(
    to bottom, 
    rgb(232, 165, 88) 0%, 
    rgb(90, 60, 30) 100%
  );
  width: 100%;
  padding: 3rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 110vh;
  position: relative;
  margin-top: -4.5vh;
}

.slogan {
  font-family: 'font', sans-serif;
  text-align: center;
  font-size: clamp(1.2rem, 3vw, 2rem);
  color: #2c1810;
  max-width: 90%;
  position: relative;
  z-index: 1;
  

  margin: 0 auto;
  padding-top: 0rem; 
}

.carousel-wrapper {
  width: 100%;
  max-width: 1200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Carousel Styles */
.carousel-container {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: clamp(300px, 50vw, 675px);
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.3);
}

.carousel-image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
  user-select: none;
  top: 0;
  left: 0;
  transition: opacity 0.6s ease-in-out;
  opacity: 0;
  z-index: 1;
}

.fade-in {
  opacity: 1;
  z-index: 2;
}

.fade-out {
  opacity: 0;
  z-index: 1;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0,0,0,0.4);
  border: none;
  color: white;
  font-size: clamp(1.5rem, 4vw, 2.5rem);
  padding: clamp(5px, 2vw, 15px);
  cursor: pointer;
  border-radius: 50%;
  user-select: none;
  transition: all 0.3s ease;
  z-index: 3;
  width: clamp(40px, 8vw, 60px);
  height: clamp(40px, 8vw, 60px);
  display: flex;
  align-items: center;
  justify-content: center;
}

.arrow:hover {
  background-color: rgba(0,0,0,0.7);
  transform: translateY(-50%) scale(1.1);
}

.left-arrow {
  left: clamp(5px, 2vw, 10px);
}

.right-arrow {
  right: clamp(5px, 2vw, 10px);
}

.content {
  padding: 2rem;
  text-align: center;
}

/* Mobile Optimizations */
@media (max-width: 768px) {
  .hero-section {
    padding: 1rem;
  }
  
  .title {
    width: 70vw;
    min-width: 200px;
    margin-bottom: 30rem;
  }
  
  .join-link {
    font-size: 1.2rem;
    bottom: 1.5rem;
  }
  
  .top-nav {
    top: 0.5rem;
    right: 0.5rem;
  }
  
  .nav-links {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .nav-item {
    padding: 0.4rem 0.8rem;
    font-size: 0.8rem;
  }
  
  .nav-icon {
    width: 20px;
    height: 20px;
  }
  
  .footer-section {
    padding: 2rem 0.5rem;
    min-height: 110vh;
    margin-top: -10vh;
  }
  
  .slogan {
    font-size: 1.1rem;
    margin-bottom: 2rem;
    padding: 0 1rem;
    z-index: 99;
  }
  
  .carousel-container {
    height: 40vh;
    min-height: 250px;
  }
  
  .arrow {
    font-size: 1.5rem;
    width: 40px;
    height: 40px;
    padding: 5px;
  }
  
  .carousel-dots {
    bottom: 0.5rem;
    gap: 0.25rem;
    max-width: 98%;
  }
  
  .dot {
    width: 8px;
    height: 8px;
    min-width: 8px;
    min-height: 8px;
    border-width: 1.5px;
  }
}

@media (max-width: 480px) {
  .title {
    width: 80vw;
    min-width: 180px;
  }
  
  .join-link {
    font-size: 1rem;
    bottom: 1rem;
  }
  
  .nav-links {
    max-width: 150px;
  }
  
  .nav-item {
    padding: 0.3rem 0.6rem;
    font-size: 0.7rem;
  }
  
  .slogan {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .carousel-container {
    height: 35vh;
    min-height: 200px;
  }
  
  .carousel-dots {
    bottom: 0.3rem;
    gap: 0.15rem;
    max-width: 99%;
  }
  
  .dot {
    width: 6px;
    height: 6px;
    min-width: 6px;
    min-height: 6px;
    border-width: 1px;
  }
}

/* Carousel dot indicators */
.carousel-dots {
  position: absolute;
  bottom: 1rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 0.5rem;
  z-index: 4;
  justify-content: center;
  max-width: 95%;
  flex-wrap: nowrap;
  overflow-x: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.carousel-dots::-webkit-scrollbar {
  display: none;
}

.dot {
  width: 12px;
  height: 12px;
  min-width: 12px;
  min-height: 12px;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.5);
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  flex-shrink: 0;
  padding: 0;
  outline: none;
}

.dot:hover {
  border-color: rgba(255, 255, 255, 0.8);
}

.dot.active {
  background: rgba(255, 255, 255, 0.9);
  border-color: rgba(255, 255, 255, 0.9);
}

.dot:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

/* Large screen optimizations */
@media (min-width: 1400px) {
  .carousel-container {
    max-width: 1200px;
    height: 675px;
  }
  
  .footer-section {
    padding: 4rem 2rem;
  }
  
  .slogan {
    font-size: 1.8rem;
    margin-bottom: 3rem;
  }
}
.join-banner {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(to right, rgb(10,10,10),rgb(0,0,0));
  color: white;
  font-family: 'font', sans-serif;
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  text-align: center;
  padding: 1rem;
  text-decoration: none;
  z-index: 1000;
  transition: background 0.3s ease;
}

.join-banner:hover {
  background: linear-gradient(to right, #111111cc, #111111ee);
}
